@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
   height: 50px;
   display: flex;
   align-items: center;
   padding: 2em;

   background: #5C9CF5;
   opacity: 1;
}

.header h1 {
   text-align: left;
   font: normal normal medium 20px/24px Helvetica Neue;
   letter-spacing: 0px;
   color: #FFFFFF;
   text-transform: uppercase;
   opacity: 1;
}

.setInfo {
   text-align: center;
   display: flex;
   flex-direction: column;
   margin-bottom: 5vh;
}

.setInfo h1 {
   margin-top: 5vh;
   margin-bottom: 2.5vh;
   font: normal normal medium 40px/49px Helvetica Neue;
   letter-spacing: 0px;
   color: #404756;
   opacity: 1;
}

.creatorInfo {
   margin: 0 auto;
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   width: 10vw;
   margin-bottom: 2.5vh;
}

.creatorInfo h3 {
   font: normal normal normal 16px/18px Helvetica Neue;
   letter-spacing: 0px;
   color: #848484;
   opacity: 1;
}

.creatorInfo h2 {
   font: normal normal normal 20px/24px Helvetica Neue;
   letter-spacing: 0px;
   color: #848484;
   opacity: 1;
}

.cueCardsList {
   background: #F0F3F6;
   display: flex;
   flex-direction: column;
}

.card {
   background-color: white;
   width: 30vw;
   height: 20vw;
   display: flex;
   justify-content: center;
   align-items: center;
   margin: 0 auto;
   box-shadow: 0px 5px 35px #0000001A;
   border: 1px solid #FAFAFA;
   margin-bottom: 2.5vh;
}

.card:hover {
   cursor: pointer;
}

.cardNav {
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: center;
}

.cardNav h1 {
   font: normal normal normal 20px/24px Helvetica Neue;
}


.bottomHalf {
   background: #F0F3F6;
   padding-bottom: 5vh;
}

.cueCardsList {
   width: 65vw;
   margin: 0 auto;
}

.listHeader {
   display: flex;
   align-items: space-between;
   /* justify-content: space-between; */
   margin-top: 86px;
}

.listHeader h1 {
   font: normal normal bold 22px/27px Helvetica Neue;
   letter-spacing: 0px;
   color: #404756;
}

.listHeader h2 {
   font: normal normal bold 22px/27px Helvetica Neue;
   letter-spacing: 0px;
   color: #848484;
}

.listHeader h1:hover {
   cursor: pointer;
}

.listHeader h2:hover {
   cursor: pointer;
}

.list {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.individualCard {
   width: 65vw;
   height: 10vw;
   background: #FFFFFF 0% 0% no-repeat padding-box;
   box-shadow: 0px 3px 6px #00000021;
   border-radius: 10px;
   display: flex;
   font:  Helvetica Neue;
   font-size: small;
   letter-spacing: 0px;
   color: #404756;
   margin: 15px;

}

.individualCard h1 {
   font-size: small;
}

.Term {
   width:40%;
   padding: 20px;
}

.Definition {
   padding: 20px;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   width: 60%;
}

.Icons {
   display: flex;
   flex-direction: row;
   width: 15%;
   justify-content: space-between;
   align-items: flex-start;
}


.Arrow:hover {
   cursor: pointer;
}
.NoteSetTile {
    width: 321px;
    height: 184px;
    background-color: white;

    border-radius: 15px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: left; 
    box-shadow:         0px 2px 10px 0px rgba(46, 46, 46, 0.16); 
}

.NoteSetTileInner {
    width: 90%;
    height: 80%;
    margin: auto;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.NoteSetTileTop {
    
}

.NoteSetTileTitle {
    font-size: 22px;

    margin-bottom: 0px;
}

.NoteSetTileTerms {
    font-size: 18px;
    color: #848484;
}

.NoteSetTileCreator {
    width: 100%;

    text-align: left;

    margin-bottom: 0px;
    padding-bottom: 0px;
}


.FileDropper {
    background-color: white;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
}

.FileInput {
    width: 50%;
}


.CreateButtons {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}
.HomePage {
    background-color: #F0F0F0;

    display: flex;
    flex-direction: row;

    min-height: 100vh;
}

.HomeNavBar {
    background-color: #5C9CF5;
}

.Sidebar {
    width: 20%;
    background-color: white;
}

.SidebarOptions {
    margin-top: 50px;
    margin-left: 10px;
    text-align: left;

    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
}

.SidebarOptionText {
    font-size: 22px;
}

.MainScreen {
    width: 100%;
    height: 100%;
    margin-top: 50px;
}

.NoteSetTiles {
    padding-top: 30px;
    margin: auto;
    
    width: 90%;
    display: grid;
    grid-gap: 30px 30px;
    gap: 30px 30px;
    grid-template-columns: repeat(auto-fit, minmax(300px,1fr));
}
html,
body {
  height: 100%;
  margin: 0;
}

#root{
    height: 100%;
    --popup-margin: 40px;
    --popup-input-height: 40px;
    --popup-input-width: 250px;
}

.NavBar{
    top: 0px;
    left: 0px;
    width: 100%;
    height: 99px;
    background: #0A7FEB 0% 0% no-repeat padding-box;
    opacity: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.navTitle{
    margin-left: 10%;
    font: normal normal 600 20px/36px Source Sans Pro;
    letter-spacing: 0px;
    color: #FFFFFF;
}

.buttonGroup{
    margin-left: auto;
    margin-right: 10%;
}

.loginButton{
    background-color: transparent;
    font-family: Arial, Helvetica, sans-serif;
    border: none;
    outline:none;
    font-size: 20px;
    color: white;
    padding: 10px;
    margin: 10px;
}

.signUpButton{
    background: #74CAFF 0% 0% no-repeat padding-box;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
    border:none;
}

.getStartedButton{
    background: #2A99FB 0% 0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    color: white;
    padding: 10px;
    border:none;
}

.pageContainer{
    height: 100%;
    padding: auto;
    display: flex;
    flex-direction: column;
}

.container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
    flex-grow: 1;
    margin: 50px;
}

.titleText{
    font-family: Arial, Helvetica, sans-serif;
    color: #707070;
    font-size: 30px;
    margin-top: 20%;
    margin-bottom: 20%;
    text-align: left;
}

.subContainter{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.landingPageImage{
    width: 500px;
    
}

.popUp{
    background-color: white;
    display:flex;
    flex-direction: column;
    border-radius: 10px;
}

.greyBackground{
    position: fixed;
    background-color: rgba(0,0,0,0.5);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.formLabel{
    margin-top: 20px;
    margin-right: var(--popup-margin);
    margin-left: var(--popup-margin);
    align-self: flex-start;
    color: #4B4B4B;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
}

.popUpInput{
    margin-top: 5px;
    width: var(--popup-input-width);
    height: var(--popup-input-height);
    margin-right: var(--popup-margin);
    margin-left: var(--popup-margin);
    padding: 0;
    align-self: center;
}

.closePopUp{
    align-self: flex-end;
    margin-top: 10px;
    margin-right: 10px;
}

.popUpSignUp{
    background: #2A99FB 0% 0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
    border: none;
    outline:none;
    width: var(--popup-input-width);
    height: var(--popup-input-height);
    margin-right: var(--popup-margin);
    margin-left: var(--popup-margin);
    margin-top: 30px;
    margin-bottom: var(--popup-margin);
    padding: 0;
    align-self: center;
}

.verticalFlexBox{
    display: flex;
}
.App {
  text-align: center;
}

